import { useConfig } from 'hooks'
import React from 'react'
import styled from 'styled-components'

interface HelpSectionProps {
  close: () => void
}

const HelpSection: React.FC<HelpSectionProps> = ({ close }) => {
  const { advancedModeEnabled } = useConfig()

  return (
    <Container>
      <div className="text-center">
        <h6>
          Qu hace el botn de <span className="text-success">atendi</span>?
        </h6>
        <p>
          Manda el nmero al final de la cola y registra la fecha en que se
          atendi.
        </p>
      </div>
      <div className="text-center">
        <h6>
          Qu hace el botn de <span className="text-danger">no atendi</span>?
        </h6>
        <p>
          Guarda el nmero para llamarlo de vuelta maana, hasta un mximo de 3
          das. Al tercer da, si no atendi, se lo manda al final de la cola.
        </p>
      </div>
      {advancedModeEnabled && (
        <>
          <div className="text-center">
            <h6>
              Qu hace el botn de{' '}
              <span className="text-primary">contestador</span>?
            </h6>
            <p>
              Congela el nmero por 10 das para darle una segunda oportunidad,
              luego lo manda al final de la cola obligatoriamente. En el caso de
              que el nmero ya fuera una rellamada (ej.: si ayer se le puso "no
              en casa"), este botn lo manda directamenet al final de la cola
              dado que ya tuve 2 oportunidades y se dejo ahora un mensaje.
            </p>
          </div>
          <div className="text-center">
            <h6>
              Qu hace el botn de <span className="text-info">aplazar</span>?
            </h6>
            <p>Congela el nmero para que se lo llame dentro de 3 das.</p>
          </div>
          <div className="text-center">
            <h6>
              Qu hace el botn de{' '}
              <span className="text-secondary">ignorar</span>?
            </h6>
            <p>Manda el nmero directamente al final de la cola.</p>
          </div>
        </>
      )}
      <div className="text-center">
        <h6>
          Qu hace el botn de <span className="text-warning">no visitar</span>
          ?
        </h6>
        <p>
          Quita el nmero de la cola. No se lo borra pero tampoco se lo volver
          a llamar.
        </p>
      </div>
      <div className="text-center">
        <h6>
          Qu hace el botn de{' '}
          <span className="text-secondary">no existe</span>?
        </h6>
        <p>
          Suspende el nmero durante 6 meses. Luego vuelve a la cola porque
          existe la posibilidad de que se haya reactivado.
        </p>
      </div>

      <p className="text-center">
        Nota: en ningn caso se borran los nmeros. Si correspondiera (por ej.
        por estar fuera del territorio), por favor, comunicarse con el siervo de
        territorios.
      </p>

      <Button className="btn btn-secondary" onClick={close}>
        Volver
      </Button>
    </Container>
  )
}

const Container = styled.div`
  display: flex;
  padding: 20px 0;
  min-height: calc(100vh - ${({ theme }) => theme.navbarHeight}px);
  justify-content: space-evenly;
  flex-direction: column;
`

const Button = styled.button`
  width: 100%;
`

export { HelpSection }
